import { makeStyles } from "@material-ui/core/styles";

export default makeStyles(theme => ({
	toolbar: theme.mixins.toolbar,
	title: {
		marginTop: "5%",
	},
	emptyButton: {
		minWidth: "150px",
		[theme.breakpoints.down("xs")]: {
			marginBottom: "5px",
			marginRight: "0px",
		},
		[theme.breakpoints.up("sm")]: {
			marginRight: "20px",
		},
	},
	checkoutButton: {
		minWidth: "150px",
	},
	link: {
		textDecoration: "none",
	},
	cardDetails: {
		display: "flex",
		marginTop: "10%",
		marginBottom: "10%",
		width: "100%",
		justifyContent: "space-between",
	},
}));
